import React, { useState, useEffect } from 'react';
import { withTranslation } from 'react-i18next';
import { GridContainer } from '@owl-material';

import DashboardSelect from './components/DashboardSelect';
import DashboardWidgets from './components/DashboardWidgets';
import DashboardService from 'services/dashboardService';

const Dashboard = ({ ...props }) => {

	const [dashboards, setDashboards ] = useState([]);

	useEffect(() => {
		const loadDashboards = async () => {
			setDashboards(await DashboardService.getDashboards());
		}

		loadDashboards();
	}, []);

	return (
		dashboards &&
		dashboards.map((dash) => (
			dash.active &&
			<GridContainer id="dashboard"  style={{ background: "#FFFFFF" }}>
				<DashboardSelect dash={dash}/>
				<DashboardWidgets widgetsOfDash={dash.widgets}/>
			</GridContainer>
		))
	)
}

export default withTranslation('common')(Dashboard);
