import React, { useState, useEffect } from 'react';
import { withTranslation } from 'react-i18next';
import { BottomNavigation,  BottomNavigationAction, Card, CardActions, CardContent, GridContainer, GridItem, Icon, PieChart, Typography } from '@owl-material';

import WidgetService from 'services/widgetService';

const DashboardWidgets = ({ widgetsOfDash, t, ...props }) => {

	const [widgets, setWidgets ] = useState([...widgetsOfDash]);

	useEffect(() => {
		let newStateWidgets = [...widgets];

		const promissesAll = newStateWidgets.map( (widget) => {
			return WidgetService.getDataToWidgets(widget) || [];
		});

		Promise.all(promissesAll)
		.then(function (results) {
			console.log("Resultado das Promisses: "+JSON.stringify(results));
			setWidgets(results);
		});
	}, [widgetsOfDash]);

	const onClickInPieChart = (id)  => {
		console.log(id);
	}

	const formatter = (value) => {
		if (value === 0) return '0 Bytes';

		const k = 1000;
		const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

		const i = Math.floor(Math.log(value) / Math.log(k));

		return parseFloat((value / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
	  }

	const goFullscreenToWidget = (cardOfWidget) => {
		document.getElementById(cardOfWidget).webkitRequestFullscreen();
	}

	return (
		<GridContainer>
			{widgets.map((widget) => (
				<GridItem xs={4} key={widget.id}>
					<Card id={'widget'+widget.id}>
						<CardActions>
							<GridContainer alignItems="center" justify="center" xs={9} >
								<GridItem>
									<Typography variant='subtitle1' index={widget.id}>{widget.name}</Typography>
								</GridItem>
							</GridContainer>
							<BottomNavigation justify="rigth" xs={3}>
								<BottomNavigationAction
									label={t('edit')}
									style={{padding: 0, minWidth: 40}}
									icon={
										<Icon style={{ fontSize: 14 }}>{'edit-rounded'}</Icon>
									}/>
								<BottomNavigationAction
									label={t('refresh')}
									style={{padding: 0, minWidth: 40}}
									icon={
										<Icon style={{ fontSize: 14}}>{'refresh-rounded'}</Icon>
									}/>

								<BottomNavigationAction
									label={t('move')}
									style={{padding: 0, minWidth: 40}}
									icon={
										<Icon style={{ fontSize: 14 }}>{'drag-indicator-rounded'}</Icon>
									}/>
								<BottomNavigationAction
									label={t('fullscreen')}
									style={{padding: 0, minWidth: 40}}
									icon={
										<Icon style={{ fontSize: 14 }}>{'fullscreen-rounded'}</Icon>
									}
									onClick={() => goFullscreenToWidget('widget'+widget.id)} />
							</BottomNavigation>
						</CardActions>
						<CardContent>
							<PieChart
								data={widget.data}
								height={'100%'}
								onClick={onClickInPieChart}
								valueFormatter={formatter}
								showLegend={false}
							/>
						</CardContent>
					</Card>
				</GridItem>
			))}
		</GridContainer>
	)
}

export default withTranslation('common')(DashboardWidgets);
